.App{
  margin: 0 auto;
  max-width: 600px;
}

.App-Header{
  display: inline-block;
  text-align: center;
}

.App-estimation-table{
  margin: 0 auto;
  text-align: left;
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-collapse: collapse;
}

.App-estimation-table *{
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.App-estimation-table td{
  padding: 1rem .4rem;
  margin: 0;
}

.App-estimation-table th{
  padding: .8rem .4rem;
  font-weight: 400;
}

.App-estimation-table tr:nth-child(even){
  background-color: #eee;
}

.App-estimation-table tr th:first-child{
  min-width: 20rem;
}

.App-estimation-table .totalEst{
  text-align: center;
  font-size: 1em;
}

input{
  border: 0;
  border-bottom: 1px #222 solid;
  padding: .2rem;
  font-size: 1em;
  background: transparent;
}

.App-estimation-input{
  width: 2rem;
  text-align: center;
}

.App-estimation-table thead tr{
  background-color: #474747;
  color: white;
}

.App-estimation-table tr.total-row{
  background: black;
  color: white;
}

.App-estimation-table tr.total-row td{
  font-weight: 700;
}

.estimation-delete{
  cursor: pointer;
}



